body {
  min-width: 320px;
  margin: 0;
}
@media (min-width: 1200px) {
  body {
    width: 1200px;
    margin: 40px auto;
    margin-top: 0;
    margin-bottom: 0;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.2);
  }
}
@font-face {
  font-family: "PT Sans";
  src: url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "PT Sans";
  src: url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.list-reset {
  margin: 0;
  padding: 0;
  list-style: none;
}
.main-nav {
  width: auto;
  margin: 0;
  position: relative;
}
.main-nav__menu {
  height: 56px;
  text-align: center;
}
.main-nav__toggle {
  border: none;
  font: inherit;
  color: inherit;
  background-color: transparent;
  position: absolute;
  right: 20px;
  bottom: 18px;
  width: 23px;
  height: 22px;
  cursor: pointer;
}
.main-nav--closed .main-nav__toggle {
  background-image: url("../img/icon-menu.svg");
  background-repeat: no-repeat;
}
.main-nav--opened .main-nav__toggle {
  top: 18px;
  right: 17px;
  background-image: url("../img/icon-cross.svg");
  background-repeat: no-repeat;
}
.main-nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
}
.main-nav__link {
  display: block;
  text-decoration: none;
  padding-top: 17px;
  padding-bottom: 14px;
  background-color: #81b3d2;
  text-align: center;
  padding-right: 4px;
  font-family: "PT Sans", "Arial", sans-serif;
  text-transform: uppercase;
  font-size: 18px;
  color: #ffffff;
  line-height: 26px;
  border-top: 1px solid #9ac2db;
  border-bottom: 1px solid #669ec0;
  position: relative;
}
.main-nav__link:hover {
  background-color: #669ec0;
}
.main-nav__link:focus,
.main-nav__link:active {
  background-color: #5496bd;
  color: rgba(255, 255, 255, 0.3);
}
.main-nav__link:disabled {
  background-color: #e5e5e5;
}
@media (max-width: 767px) {
  .main-nav--closed .main-nav__list {
    display: none;
  }
  .main-nav--nojs .main-nav__toggle {
    display: none;
  }
  .main-nav--nojs .main-nav__list {
    display: block;
  }
}
@media (min-width: 768px) {
  .main-nav__list {
    flex-direction: row;
  }
  .main-nav__item {
    flex-basis: 25%;
  }
  .main-nav__item--photos {
    margin-right: 10%;
  }
  .main-nav__item--form {
    margin-left: 10%;
  }
  .main-nav__link {
    background-color: #ffffff;
    padding: 0;
    padding-top: 15px;
    padding-bottom: 15px;
    font-family: "PT Sans", "Arial", sans-serif;
    text-transform: uppercase;
    font-size: 14px;
    color: #000000;
    font-weight: bold;
    border: none;
  }
  .main-nav__link:hover {
    background-color: #ffffff;
    color: #81b3d2;
  }
  .main-nav__link:active {
    color: #b2b2b2;
  }
  .main-nav__link--active {
    color: #827065;
  }
  .main-nav__link--active:hover {
    color: #75655b;
  }
  .main-nav__link--active:active {
    color: #d7d2cf;
  }
  .main-nav__menu {
    position: absolute;
    top: 0;
    right: -50px;
    margin-right: 50%;
  }
  .main-nav__toggle {
    display: none;
  }
}
@media (min-width: 1200px) {
  .main-nav {
    width: 1200px;
    margin: 0 auto;
  }
  .main-nav__menu {
    right: -92px;
  }
  .main-nav__image {
    display: block;
    width: 188px;
    height: 96px;
  }
}
